<template>
  <el-form :inline="true" :model="query">
    <el-form-item label="QuestionId">
      <el-input v-model="query.qid" placeholder="QuestionId"></el-input>
    </el-form-item>
    <el-form-item label="Param">
      <el-input v-model="query.param" placeholder="Param"></el-input>
    </el-form-item>
    <el-form-item label="Weight">
      <el-input v-model="query.weight" placeholder="Weight"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="getPageByCondition">Search</el-button>
    </el-form-item>
    <el-form-item>
      <el-button type="success" @click="showDialogAdd">New</el-button>
    </el-form-item>
  </el-form>

  <el-table :data="paramList" style="width: 100%">
    <el-table-column prop="id" label="ID" width="50" />
    <el-table-column prop="qid" label="QuestionId" width="150" />
    <el-table-column prop="param" label="Param" width="200" />
    <el-table-column prop="weight" label="Weight" width="150" />
    <el-table-column prop="answer1" label="Answer1" width="100" />
    <el-table-column prop="answer2" label="Answer2" width="100" />
    <el-table-column prop="answer3" label="Answer3" width="100" />
    <el-table-column prop="answer4" label="Answer4" width="100" />
    <el-table-column prop="answer5" label="Answer5" width="100" />
    <el-table-column>
      <template #default="scope">
        <el-button type="warning" @click="showDialogEdit(scope.row)"
          >Edit</el-button
        >
        <el-button type="danger" @click="showDialogDelete(scope.row)"
          >Delete</el-button
        >
      </template>
    </el-table-column>
  </el-table>
  <el-pagination
    class="pagination"
    :currentPage="pagination.currentPage"
    :page-size="pagination.pageSize"
    :total="pagination.total"
    @current-change="handleCurrentChange"
    layout="total, prev, pager, next, jumper"
  ></el-pagination>

  <el-dialog v-model="dialogAddVisible" title="New">
    <el-form :model="formData">
      <el-form :inline="true">
        <el-form-item label="QuestionId">
          <el-input v-model="formData.qid"></el-input>
        </el-form-item>
        <el-form-item label="Weight">
          <el-input v-model="formData.weight"></el-input>
        </el-form-item>
      </el-form>
      <el-form-item label="Param">
        <el-input autosize type="textarea" v-model="formData.param"></el-input>
      </el-form-item>
      <el-form :inline="true">
        <el-form-item label="Answer1">
          <el-checkbox v-model="formData.answer1"></el-checkbox>
        </el-form-item>
        <el-form-item label="Answer2">
          <el-checkbox v-model="formData.answer2"></el-checkbox>
        </el-form-item>
        <el-form-item label="Answer3">
          <el-checkbox v-model="formData.answer3"></el-checkbox>
        </el-form-item>
        <el-form-item label="Answer4">
          <el-checkbox v-model="formData.answer4"></el-checkbox>
        </el-form-item>
        <el-form-item label="Answer5">
          <el-checkbox v-model="formData.answer5"></el-checkbox>
        </el-form-item>
      </el-form>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="cancel">Cancel</el-button>
        <el-button type="primary" @click="checkDialogAdd">OK</el-button>
      </span>
    </template>
  </el-dialog>

  <el-dialog v-model="dialogEditVisible" title="Edit">
    <el-form :model="formData">
      <el-form :inline="true">
        <el-form-item label="QuestionId">
          <el-input v-model="formData.qid"></el-input>
        </el-form-item>
        <el-form-item label="Weight">
          <el-input v-model="formData.weight"></el-input>
        </el-form-item>
      </el-form>
      <el-form-item label="Param">
        <el-input autosize type="textarea" v-model="formData.param"></el-input>
      </el-form-item>
      <el-form :inline="true">
        <el-form-item label="Answer1">
          <el-checkbox v-model="formData.answer1"></el-checkbox>
        </el-form-item>
        <el-form-item label="Answer2">
          <el-checkbox v-model="formData.answer2"></el-checkbox>
        </el-form-item>
        <el-form-item label="Answer3">
          <el-checkbox v-model="formData.answer3"></el-checkbox>
        </el-form-item>
        <el-form-item label="Answer4">
          <el-checkbox v-model="formData.answer4"></el-checkbox>
        </el-form-item>
        <el-form-item label="Answer5">
          <el-checkbox v-model="formData.answer5"></el-checkbox>
        </el-form-item>
      </el-form>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="cancel">Cancel</el-button>
        <el-button type="primary" @click="checkDialogEdit">OK</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ElMessage, ElMessageBox } from "element-plus";

export default {
  data() {
    return {
      paramList: [],
      query: {},

      formData: {
        answer1: false,
        answer2: false,
        answer3: false,
        answer4: false,
        answer5: false,
      },
      dialogAddVisible: false,
      dialogEditVisible: false,

      pagination: {
        currentPage: 3,
        pageSize: 10,
        total: 21,
      },
    };
  },
  created() {
    this.getPageByCondition();
  },
  methods: {
    getAll() {
      axios.get("/background/params").then((res) => {
        if (res.data.code === 0) this.paramList = res.data.data;
        else ElMessage.error(res.data.msg);
      });
    },
    getPageByCondition() {
      axios
        .get(
          "/background/params/" +
            this.pagination.currentPage +
            "/" +
            this.pagination.pageSize,
          {
            params: this.query,
          }
        )
        .then((res) => {
          if (res.data.code === 0) {
            this.paramList = res.data.data.records;
            this.pagination.total = res.data.data.total;
            this.pagination.currentPage = res.data.data.current;
            this.pagination.pageSize = res.data.data.size;
          } else ElMessage.error(res.data.msg);
        });
    },
    cancel() {
      this.dialogAddVisible = false;
      this.dialogEditVisible = false;
      this.formData = {
        answer1: false,
        answer2: false,
        answer3: false,
        answer4: false,
        answer5: false,
      };
    },
    showDialogAdd() {
      this.dialogAddVisible = true;
      this.formData = {
        answer1: false,
        answer2: false,
        answer3: false,
        answer4: false,
        answer5: false,
      };
    },
    checkDialogAdd() {
      axios
        .post("/background/params", this.formData)
        .then((res) => {
          if (res.data.code === 0) {
            this.dialogAddVisible = false;
            ElMessage.success(res.data.msg);
          } else ElMessage.error(res.data.msg);
        })
        .finally(() => {
          this.getPageByCondition();
        });
    },
    showDialogEdit(row) {
      this.formData = {
        answer1: false,
        answer2: false,
        answer3: false,
        answer4: false,
        answer5: false,
      };
      axios
        .get("/background/params/" + row.id)
        .then((res) => {
          if (res.data.code === 0) {
            this.formData = res.data.data;
            this.dialogEditVisible = true;
          } else ElMessage.error(res.data.msg);
        })
        .finally(() => {
          this.getPageByCondition();
        });
    },
    checkDialogEdit() {
      axios
        .put("/background/params", this.formData)
        .then((res) => {
          if (res.data.code === 0) {
            this.dialogEditVisible = false;
            ElMessage.success(res.data.msg);
          } else ElMessage.error(res.data.msg);
        })
        .finally(() => {
          this.getPageByCondition();
        });
    },
    showDialogDelete(row) {
      ElMessageBox.confirm("Confirm Delete?", "Warning", {
        confirmButtonText: "Confirm",
        cancelButtonText: "Cancel",
        type: "warning",
      }).then(() => {
        axios
          .delete("/background/params/" + row.id)
          .then((res) => {
            if (res.data.code === 0) ElMessage.success(res.data.msg);
            else ElMessage.error(res.data.msg);
          })
          .finally(() => {
            this.getPageByCondition();
          });
      });
    },
    handleCurrentChange(currentPage) {
      this.pagination.currentPage = currentPage;
      this.getPageByCondition();
    },
  },
};
</script>

<style scoped>
.dialog-footer button:first-child {
  margin-right: 10px;
}

.pagination {
  margin-top: 10px;
  float: right;
}
</style>
